import React, { Component } from 'react';
import './Comp.css';
import Thumbnail from './Thumbnail.js'


export default class Project extends Component{
  constructor(props)
  {
    super(props)
    this.showProjectDiv = this.showProjectDiv.bind(this)
    this.hideProjectDiv = this.hideProjectDiv.bind(this)
    this.onLeftArrowClicked = this.onLeftArrowClicked.bind(this)
    this.onRightArrowClicked = this.onRightArrowClicked.bind(this)
    this.state = {
      currentProject: {
        name: "",
        pics : [],
        currentNumber: 0,
        logo: "",
        githubLink : "",
        downloadLink : "",
        liveLink : ""
      },
      shown : "hidden",
      smartNotes: {
        name : "smartnotes",
        pics : [
          "https://raw.githubusercontent.com/JonathanMFleming/WebImagestorage/master/smartnotes1.png",
          "https://raw.githubusercontent.com/JonathanMFleming/WebImagestorage/master/smartnotes2.png",
          "https://raw.githubusercontent.com/JonathanMFleming/WebImagestorage/master/smartnotes3.png",
          "https://raw.githubusercontent.com/JonathanMFleming/WebImagestorage/master/smartnotes4.png",
          "https://raw.githubusercontent.com/JonathanMFleming/WebImagestorage/master/smartnotes5.png",
          
        ],
        currentNumber : 0,
        logo : "https://raw.githubusercontent.com/18932508/smartnotes/master/src/smartNotesLogo.png",
        githubLink : "",
        downloadLink : "",
        liveLink : ""
      },
      bugTracker: {
        name : "BugTracker",
        pics : [
        ],
        currentNumber : 0,
        logo : "https://raw.githubusercontent.com/JonathanMFleming/BugTracker/master/src/components/bugIcon.png",
        githubLink : "https://github.com/JonathanMFleming/BugTracker",
        downloadLink : "",
        liveLink : ""
      }
    }
  }
  onLeftArrowClicked()
  {
    this.setState(prevState =>({
      currentProject:{
          ...prevState.currentProject,
          currentNumber : this.state.currentProject.currentNumber-1}
    }))
  }
  onRightArrowClicked()
  {
    this.setState(prevState =>({
      currentProject:{
          ...prevState.currentProject,
          currentNumber : this.state.currentProject.currentNumber+1}
    }))
  }
  showProjectDiv(name)
  {
    this.setState({
      currentProject : name,
      shown: "visible"
    })
  }
  hideProjectDiv(e)
  {
    this.setState({
      shown: "hidden"
    })
  }

  render(){
  return (
    <div class="projectDiv">
        <div class="projectDivRow">
        <Thumbnail  object={this.state.smartNotes} showProjectDiv={this.showProjectDiv}/>
        <Thumbnail object={this.state.bugTracker} showProjectDiv={this.showProjectDiv} />
        </div>
        <div class="projectDivRow">
        </div>
        <div class="projectDisplayDiv" style={{visibility: this.state.shown}} onClick={this.hideProjectDiv}></div>
        <div class="projectDiv2" style={{visibility: this.state.shown}}>
          <div class="projectWrapper">
            <div class="projectTitle">
              <img class="projectLogo" src={this.state.currentProject.logo} />
              <div class="projectName">{this.state.currentProject.name}</div>
              <div class="projectX"><button class="projectXButton" onClick={this.hideProjectDiv}>X</button></div>
            </div>
            <div class="projectMain">
              <div class="arrowButtonLeft"><button class="arrowButton" onClick={this.onLeftArrowClicked} disabled={this.state.currentProject.currentNumber===0}>&larr;</button></div>
              <div class="projectPicDiv">
                <img class="projectPic" src={this.state.currentProject.pics[this.state.currentProject.currentNumber]} />
              </div>
              <div class="arrowButtonRight"><button class="arrowButton" onClick={this.onRightArrowClicked} disabled={this.state.currentProject.currentNumber===this.state.currentProject.pics.length-1}>&rarr;</button></div>
            </div>
            <div class="projectBottom">
              <div>{this.state.currentProject.githubLink}</div>
              <div>link</div>
              <div>something</div>
            </div>
          </div>
        </div>
    </div>
  );
}
}