import React, { Component } from 'react';
import './Comp.css';
import smartNotesLogo from './smartNotesLogo.png';


export default class Thumbnail extends Component{
  constructor(props)
  {
    super(props)
    this.state = {
    }
  }
  render(){
  return (
   <div onClick={() => this.props.showProjectDiv(this.props.object)} >
    <div class="thumbnailDiv">
        <img class="thumbnailLogo" src={this.props.object.logo} alt="img"  />
        <div class="thumbnailAnimation">
        <div class="thumbnailTitleScreen"></div>
        <div class="thumbnailTitle">{this.props.object.name.toUpperCase()}</div>
        </div>
    </div>
    </div>
  );
}
}