import React, { Component } from 'react';
import computerimage from './computerimage.png';
import './App.css';
import About from './component/About.js'
import Project from './component/Project.js'
import Languages from './component/Languages.js'


export default class App extends Component{
  constructor(props)
  {
    super(props)
    this.changeTab = this.changeTab.bind(this)
    this.checkActive = this.checkActive.bind(this)
    this.swapActive = this.swapActive.bind(this)
    this.state = {
      activeTab : "main"
    }
  }
  changeTab(e)
  {
    this.setState({
      activeTab:e.target.id
    })
  }
  checkActive(e)
  {
    if(e === this.state.activeTab)
    {
      return "active"
    }
    return ""
  }
  swapActive()
  {
    if (this.state.activeTab === "about")
    {
      return <About />
    }
    else if (this.state.activeTab === "languages")
    {
      return <Languages />
    }
    else if (this.state.activeTab === "projects")
    {
      return <Project />
    }
    else
    {
      return <img className="imageHome"src={computerimage} alt="img" />
    }
  }
  render(){
    console.log(this.state.activeTab)
  return (
    <div className="App">
      <div className="Wrapper">
            <div className="divLeft">
              <div className="divLeftContent">
                <div className="line-text">Hello, I'm</div>
                <div class="glitch" data-text="JONATHAN FLEMING">JONATHAN FLEMING</div>
                <div class="TypeWriter" data-testid="typewriter-wrapper">
                  <span class="Typewriter__wrapper">COMSCI GRADUATE</span>
                  <span class="Typewriter__cursor">|</span>
                </div>
                <button class="hover-button">
                  <span>
                    Download CV
                  </span>
                </button>
              </div>
            </div>
            <div className="divRight">
              <div class="buttonDiv">
                <button><span class={this.checkActive("main")} id="main" onClick={this.changeTab}>MAIN</span></button>
                <button><span class={this.checkActive("about")} id="about" onClick={this.changeTab}>ABOUT</span></button>
                <button><span class={this.checkActive("languages")} id="languages" onClick={this.changeTab}>LANGUAGES/FRAMEWORKS</span></button>
                <button><span class={this.checkActive("projects")} id="projects" onClick={this.changeTab}>PROJECTS</span></button>
              </div>
              <div class="wot">
              {this.swapActive()}
              </div>
            </div>
      </div>

    </div>
  );
}
}
